import { Component, OnChanges, Input, Output, EventEmitter } from '@angular/core';
import { Ingredient } from "../recipes/ingredient";
import { ShoppingListService } from "./shopping-list.service";

@Component({
  selector: 'app-shopping-list-add',
  templateUrl: './shopping-list-add.component.html'
})
export class ShoppingListAddComponent implements OnChanges {
  @Input() item: Ingredient; //can be set from outside the component for editing mode
  @Output() cleared = new EventEmitter();
  isAdd = true;

  constructor(private sls: ShoppingListService) { }
  
  ngOnChanges(changes){   //whenever item is changed from OUTSIDE (not inside)
    if(changes.item.currentValue === null){ //initial state
      this.isAdd =  true;
      this.item = {name:null, amount:null}; //so that item is not null and it doesnt give error when trying to access item.name and item.amount in ngModel
    } else{                   // i.e. item chaged from outside
      this.isAdd =  false;
    }
  }

  onSubmit(ingredient: Ingredient){
    const newIngredient = new Ingredient(ingredient.name, ingredient.amount);
      if(!this.isAdd){  //editing mode
        this.sls.editItem(this.item, newIngredient);
        this.onClear();
      }
      else{
        this.item = newIngredient;
        this.sls.addItem(this.item);
      }
  }
  onDelete(){
    this.sls.deleteItem(this.item);
    this.onClear();
  }
  onClear(){
    this.isAdd = true;
    this.cleared.emit(null);
  }


}



